@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Roboto:wght@300;400;700&display=swap');

/* Global Reset */
body, html {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background-color: #d44b3d; 
  font-family: 'Roboto', sans-serif;
  /* Allow vertical scroll if needed */
  overflow-x: hidden;
  overflow-y: auto;
}

/* Main App Container */
.app-container {
  position: relative;
  width: 100vw;
  min-height: 100vh; 
  display: flex;
  justify-content: center;
  align-items: flex-start; /* Adjust to top, so scrolling is natural if content exceeds screen height */
  text-align: center;
  padding: 20px; /* Ensures red margin around the card on all sides */
  box-sizing: border-box;
}

/* Snowflake */
.snowflake {
  position: absolute;
  background-color: white;
  border-radius: 50%;
  opacity: 0;
  animation: fall linear infinite, sway 3s ease-in-out infinite;
  animation-fill-mode: forwards;
  border: 1px solid rgba(200, 200, 200, 0.4); /* Visibility on white background */
}

/* Vertical Falling Animation */
@keyframes fall {
  0% {
    top: -100px;
    opacity: 0;
  }
  10% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    top: 100vh;
    opacity: 1;
  }
}

/* Horizontal Swaying Animation */
@keyframes sway {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
}

/* Festive Card Styling */
.card-content {
  position: relative;
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid #e3d5c9;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  padding: 2rem 3rem;
  width: 90%;          /* Fluid width that adapts to screen */
  margin: 20px auto;   /* Margin to ensure red background is visible around the card */
  box-sizing: border-box;
  z-index: 10;
  max-width: 600px;
}

/* Adjust padding on smaller screens for better fit */
@media (max-width: 600px) {
  .card-content {
    padding: 1rem;
  }
}

.card-content h1 {
  font-family: 'Great Vibes', cursive;
  font-size: 3rem;
  color: #d44b3d;
  margin-bottom: 1rem;
}

.card-content p {
  font-size: 1.2rem;
  color: #5a4d41;
  margin: 0.5rem 0;
}

.card-content p:last-child {
  margin-top: 1.5rem;
  font-weight: bold;
}

/* Rounded Image */
.rounded-image {
  width: 200px;
  height: auto;
  margin-bottom: 1rem;
  border-radius: 150px;  
  box-shadow: 0 5px 10px rgba(0,0,0,0.1);
}
